@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
/* Dracula Color Pallete */
@font-face {
  font-family: 'Bodoni';
  src: url(/everytour.id/static/media/BodoniFLF-Roman.0355cbbf.woff) format("woff"), url(/everytour.id/static/media/BodoniFLF-Roman.a5c77dca.ttf) format("trutype"); }

body, html {
  background-color: #f3b73f;
  color: #362907;
  font-family: "Lato", "sans-serif";
  font-size: 15px;
  margin: 0;
  overflow-x: hidden;
  scrollbar-width: none;
  padding: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

a {
  color: #362907;
  text-decoration: none; }

